
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import tools from '../../lib/tools.js';
export default {
name:'famlySickness',
components:{Group,Flexbox, FlexboxItem,Toast,XInput,Swipeout, SwipeoutItem, SwipeoutButton, Checker, CheckerItem,XDialog},
directives: {TransferDom},
data(){
return {
id:'',
info:{},
showPopup:false,
showToast:false,
toastMsg:'',
name:'',
dictionary:[],
tempCheckArr:[],
dictionaryObj:{},
otherSickness:'',
showOtherSickness:false,
}
},
mounted(){
var vm = this;
this.initDictionary();
this.id = (localStorage.getItem('md') || tools.getRequestParam('managerId') || tools.getCookie('user_id')); // md===userid
if(!this.id || this.id=='undefined'){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}
this.load(this.id);
},
methods:{
load(id){
var vm = this;
this.$http.post('/apiUser/queryAccount?managerId='+id,{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data){
if(data.data.mobile){
data.data.mobile = data.data.mobile.substr(0,3)+'****'+data.data.mobile.substr(7);
}
if(Object.prototype.toString.call(data.data.familyHistory) === "[object String]"){
data.data.familyHistory = JSON.parse(data.data.familyHistory);
}else{
data.data.familyHistory = [];
}
vm.info = data.data;
console.log(vm.info);
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
},
deleteFn(name){
console.log(name);
var vm = this;
let tempArr = Array.prototype.slice.call(vm.info.familyHistory).filter(item => item.name != name);
let data = {managerId:vm.id,familyHistory:JSON.stringify(tempArr)};
this.$http.axios({method:'POST',url:'/apiUser/update',data}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200) && data.resultCode){
vm.tempCheckArr = [];
vm.name = '';
vm.load(vm.id);
vm.showToast = true;
vm.toastMsg = '操作成功'
}else{
console.error(data);
vm.showToast = true;
vm.toastMsg ='保存失败';
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '操作失败';
})
},
saveFn(){
var vm = this;
if(!this.id){return}
if(!this.name || this.tempCheckArr.length==0){
this.showToast = true;
this.toastMsg = '请将信息填写完整';
return false;
}
if(Array.prototype.slice.call(this.info.familyHistory).filter(item => item.name == vm.name).length>0){
this.showToast = true;
this.toastMsg = '关系名称重复，请重新填写';
return false;
}
this.tempCheckArr = this.tempCheckArr.filter((item) => {
return (item != '11' && item != '12')
});
this.info.familyHistory.push({name:this.name,sickness:this.tempCheckArr,other:this.otherSickness})
let data = {managerId:this.id,familyHistory:JSON.stringify(this.info.familyHistory)};

this.$http.axios({method:'POST',url:'/apiUser/update',data}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200) && data.resultCode){
vm.tempCheckArr = [];
vm.otherSickness = '';
vm.name = '';
vm.showPopup = false;
vm.load(vm.id);
vm.showToast = true;
vm.toastMsg = '操作成功'
}else{
console.error(data);
vm.showToast = true;
vm.toastMsg ='保存失败';
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '操作失败';
})
},
initDictionary(){
let vm = this;

this.$http.get('/api/contracte/queryDictionary?typeId=3',{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
let tempDictionary = [],tempDict = {};
for(let obj of data.data){
tempDictionary.push({itemId:obj.itemId+'',itemName:obj.itemName});
tempDict[obj.itemId] = obj.itemName;
}
vm.dictionary = tempDictionary;
vm.dictionaryObj = tempDict;


}else{
console.error(data);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.error(error);
vm.showToast = true;
vm.toastMsg = '操作失败';
})
},
sicknessToCH(item){
let arr = item.sickness;
let vm = this;
if(Array.isArray(arr)){
let tempArr = [];
for(let item of arr){
tempArr.push(vm.dictionaryObj[item]);
}
item.other && tempArr.push(item.other);
return tempArr.join(',')
}
},
watchCheckArr(n,o){
console.log('watchCheckArr',n,o);
}
},
watch:{
tempCheckArr:{
handler(curVal,oldVal){
if(Array.prototype.slice.call(curVal).length>1 && Array.prototype.slice.call(curVal).filter(item => item=='0').length>0){
this.tempCheckArr = curVal[Array.prototype.slice.call(curVal).length-1]=='0'?['0']:Array.prototype.slice.call(curVal).filter(item => item!='0');
}
this.showOtherSickness = Array.prototype.slice.call(curVal).filter(item => (item=='11' || item=='12')).length>0?true:false;
},
deep:true
}
}
}



